<template>
    <yd-layout class="box_ziliao">
        <navBar title="安全资料" slot="navbar"></navBar>
            <yd-tab active-color="#1D91E6" font-size="0.32rem">
                <yd-tab-panel label="安全知识大数据库">
                    <div class="list" @click="onPressMenu(vo.TITLE,vo.CONTENT)" v-for="(vo,index) in knowledge" :key="index"><span>{{index+1}}、{{vo.TITLE}}</span></div>
                </yd-tab-panel>
                <yd-tab-panel label="企业安全资料库">
                    <div class="list" @click="onPressMenu(vo.TITLE,vo.CONTENT)" v-for="(vo,index) in organiztion" :key="index"><span>{{index+1}}、{{vo.TITLE}}</span></div>
                </yd-tab-panel>
            </yd-tab>
        <div class="div_list">
        </div>
    </yd-layout>
</template>

<script>
    import {api_anquanziliao} from "../../components/APIKeys";

    export default {
        name: "ziliao",
        data:function () {
            return{
                // token:window.localStorage.getItem('token'),
                knowledge:[],//安全知识数据
                organiztion:[],//企业安全数据
            }
        },
        mounted(){
          this.doLoadData();
        },
        methods:{
            onPressMenu(title,content){
                window.localStorage.setItem('title',title);
                window.localStorage.setItem('content',content);
                this.$pushRoute('ziliaocon');
            },
            doLoadData(){
                this.$showDialog();
                let jsonData = {};
                this.$post(api_anquanziliao, jsonData).then(res=>{
                    this.$showDialog(false);
                    if(res.errno == 0){
                        this.knowledge = res.data.normal || [];
                        this.organiztion = res.data.qiye || [];
                    }else{
                        this.$showError('获取信息失败');
                    }
                })
            }
        }
    }
</script>

<style scoped>

    .div_list{
        padding:0.4rem;
    }
    .list{
        padding: 0.4rem;
        display: flex;
        align-items: center;
        height: 0.8rem;
        border-bottom: 0.02rem solid #BBBBBB;
        white-space: nowrap;
        text-overflow:ellipsis;
        overflow:hidden;
    }
    .box_ziliao{
        background: #fff;
    }
</style>



